import React from 'react';
import {withRouter} from 'react-router-dom'
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import AppBar from 'material-ui/AppBar';
import {CreateResult, EditGamanec, PaymentResult} from "./PPayment";
import {Home} from "./Home";
import {PaymentForm, } from "./PaymentForm";
import {PocketsList} from "./PocketsList";
import {CreatePocket} from "./CreatePocket";
import {PocketPage} from "./PocketPage";
import {Route, Switch} from 'react-router-dom'

const pathMap = {
    '/': ' - Головна сторінка',
    '/new': ' - Створити кампанію',
    '/edit': ' - Редагування кампанії',
    '/payment': ' - Сторінка платежу',
    '/list': ' - Список зареєстрованих гаманців'
};
export const pocketMock = {
    name: 'Прибирання двору',
    description: 'Для вивезення мусору нам потрібно зібрати 1500 грн. Просимо кожного мешканця здати по 50грн. В коментаріях до платежу зазначайте будь ласка своє призвище',
    id: '3443',
    finishDate: '25/09/2018',
    finishSum: 1500,
    isPublic: true,
    payments: [{comment: "45545", cardNumber: "556784949", from: "від Крючко", email: "4545", paymentId: 98165, pocketId: "6335", subscribe: true, sum: "100", time: 'Thu Apr 12 2018 14:25:18 GMT+0300 (EEST)'}],
    reports: [],
    login: 'salt',
    password: 'salt',
    orgName: 'Ірина Журба',
    orgContacts: 'example@gmail.com',
    cardNumber: '55665757',
    group: 'Будинок №56',
    isTimeLimited: true,
    isSumLimited: true
};

const AppBarHeader = (props) => {
    return (
        <AppBar
            title={`Публічний Гаманець ${pathMap[props.location.pathname] || ''}`}
            onTitleClick={() => props.history.push('/')}
            titleStyle={{cursor: 'pointer'}}
            showMenuIconButton={false}
        />
    )
};
const Header = withRouter(AppBarHeader);

export class App extends React.Component {
    state = JSON.parse(sessionStorage.getItem('ppocket')) || {
        pockets:  [{...pocketMock}]
    };

    createPocket = (pocket) => {
        let newPockets = [...(this.state.pockets || [])];
        newPockets.push(pocket);
        this.setState({pockets: newPockets});
        this.saveInStorage()
    };

    createPayment = (pocketId, payment) => {
        let newPockets = [...this.state.pockets];
        const pocket = newPockets.find(p => p.id == pocketId);
        if (pocket) {
            pocket.payments.push(payment);
            this.setState({pockets: newPockets})
        }
        this.saveInStorage()
    };

    componentWillUnmount() {
        this.saveInStorage()
    }
    saveInStorage = () => setTimeout(() => sessionStorage.setItem('ppocket', JSON.stringify(this.state)) , 100);

    render() {
        const { pockets = [] } = this.state;
        return (
            <MuiThemeProvider>
                <div>
                    <Header/>
                    <div className='page'>
                        <Switch>
                            <Route exact path='/' component={(props)=><Home {...props} pockets={pockets} demoId={(pockets[0] || {}).id}/>}/>
                            <Route path='/new'
                                   component={(props) => <CreatePocket {...props} onSave={this.createPocket}/>}/>
                            <Route path='/edit/:id' component={(props) => <EditGamanec {...{...props, pockets}}/>}/>
                            <Route path='/result/:id' component={CreateResult}/>
                            <Route path='/pocket/:id' component={(props) => <PocketPage {...{...props, pockets}}/>}/>
                            <Route path='/list' component={(props) => <PocketsList {...{...props, pockets}}/>}/>
                            <Route path='/payment/:id' component={(props) => <PaymentForm {...props} onPay={this.createPayment}/>}/>
                            <Route path='/payment_result/:id' component={PaymentResult}/>
                        </Switch>
                    </div>
                </div>
            </MuiThemeProvider>
        )
    }
};


